'use client';

import { useEffect, useState } from 'react';
import { useLanguage } from '@/app/context/language-context';
import api from '@/lib/api';
import { Button } from '@/components/ui/button';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { LogOut, Wallet, TrendingUp, TrendingDown, Calendar } from 'lucide-react';
import { toast } from 'sonner';
import { AddExpenseDialog } from '@/components/expenses/add-expense-dialog';
import { ExpenseList } from '@/components/expenses/expense-list';

interface Expense {
  _id: string;
  title: string;
  amount: number;
  category: string;
  date: string;
  description?: string;
}

export default function Dashboard() {
  const { t } = useLanguage();
  const [expenses, setExpenses] = useState<Expense[]>([]);
  const [loading, setLoading] = useState(true);
  const [user, setUser] = useState<{ fullName: string; email: string } | null>(null);

  useEffect(() => {
    const userData = localStorage.getItem('user');
    if (userData) {
      setUser(JSON.parse(userData));
    }
    fetchExpenses();
  }, []);

  const fetchExpenses = async () => {
    try {
      const response = await api.get('/transactions');
      setExpenses(response.data);
    } catch (error) {
      console.error('Error fetching expenses:', error);
      toast.error('Failed to load expenses');
    } finally {
      setLoading(false);
    }
  };

  const handleLogout = () => {
    localStorage.removeItem('token');
    localStorage.removeItem('user');
    window.location.href = '/';
  };

  const totalExpenses = expenses.reduce((acc, curr) => acc + curr.amount, 0);

  return (
    <div className="min-h-screen bg-background p-6 lg:p-10 transition-colors duration-300">
      <div className="max-w-7xl mx-auto space-y-8">
        {/* Header */}
        <header className="flex flex-col md:flex-row justify-between items-start md:items-center gap-4 animate-in fade-in slide-in-from-top-4 duration-500">
          <div>
            <h1 className="text-4xl font-bold tracking-tight text-gradient">
              {t('dashboard.welcome')}, {user?.fullName || 'User'}
            </h1>
            <p className="text-muted-foreground mt-1 text-lg">
              {t('dashboard.overview')}
            </p>
          </div>
          <div className="flex items-center gap-4">
            <AddExpenseDialog onExpenseAdded={fetchExpenses} />
            <Button
              variant="outline"
              onClick={handleLogout}
              className="border-destructive/20 hover:bg-destructive/10 hover:text-destructive transition-colors"
            >
              <LogOut className="mr-2 h-4 w-4" /> {t('auth.logout')}
            </Button>
          </div>
        </header>

        {/* Stats Cards */}
        <div className="grid gap-6 md:grid-cols-2 lg:grid-cols-3 animate-in fade-in slide-in-from-bottom-4 duration-500 delay-150">
          <Card className="glass-card border-l-4 border-l-primary overflow-hidden relative group">
            <div className="absolute right-0 top-0 h-full w-24 bg-gradient-to-l from-primary/10 to-transparent group-hover:w-32 transition-all duration-500" />
            <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
              <CardTitle className="text-sm font-medium text-muted-foreground">
                {t('dashboard.totalExpenses')}
              </CardTitle>
              <div className="h-8 w-8 rounded-full bg-primary/10 flex items-center justify-center text-primary">
                <Wallet className="h-4 w-4" />
              </div>
            </CardHeader>
            <CardContent>
              <div className="text-3xl font-bold text-foreground">
                ${totalExpenses.toFixed(2)}
              </div>
              <p className="text-xs text-muted-foreground mt-1 flex items-center">
                <TrendingUp className="h-3 w-3 mr-1 text-emerald-500" />
                <span className="text-emerald-500 font-medium">+2.5%</span> from last month
              </p>
            </CardContent>
          </Card>

          {/* Placeholder for future stats */}
          <Card className="glass-card border-l-4 border-l-purple-500 overflow-hidden relative group">
            <div className="absolute right-0 top-0 h-full w-24 bg-gradient-to-l from-purple-500/10 to-transparent group-hover:w-32 transition-all duration-500" />
            <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
              <CardTitle className="text-sm font-medium text-muted-foreground">
                Monthly Budget
              </CardTitle>
              <div className="h-8 w-8 rounded-full bg-purple-500/10 flex items-center justify-center text-purple-500">
                <Calendar className="h-4 w-4" />
              </div>
            </CardHeader>
            <CardContent>
              <div className="text-3xl font-bold text-foreground">
                $2,000.00
              </div>
              <p className="text-xs text-muted-foreground mt-1">
                <span className="font-medium text-foreground">65%</span> used
              </p>
            </CardContent>
          </Card>
        </div>

        {/* Main Content Area */}
        <div className="grid gap-8 lg:grid-cols-3 animate-in fade-in slide-in-from-bottom-8 duration-700 delay-300">
          {/* Recent Transactions */}
          <div className="lg:col-span-2 space-y-6">
            <div className="flex items-center justify-between">
              <h2 className="text-2xl font-semibold tracking-tight">
                {t('dashboard.recentTransactions')}
              </h2>
              <Button variant="ghost" size="sm" className="text-primary">
                View All
              </Button>
            </div>

            <Card className="glass-card border-0 shadow-lg">
              <CardContent className="p-0">
                {loading ? (
                  <div className="p-8 text-center text-muted-foreground">Loading...</div>
                ) : (
                  <ExpenseList expenses={expenses} onExpenseDeleted={fetchExpenses} />
                )}
              </CardContent>
            </Card>
          </div>

          {/* Side Panel (Suggestions/Charts) */}
          <div className="space-y-6">
            <h2 className="text-2xl font-semibold tracking-tight">
              Insights
            </h2>
            <Card className="glass-card border-0 shadow-lg bg-gradient-to-br from-indigo-500/10 to-purple-500/10">
              <CardHeader>
                <CardTitle className="text-lg">AI Suggestions</CardTitle>
              </CardHeader>
              <CardContent>
                <p className="text-sm text-muted-foreground">
                  Your spending on <strong>Food</strong> is 15% higher than usual. Consider cooking at home more often.
                </p>
                <Button className="w-full mt-4" variant="secondary">
                  View Details
                </Button>
              </CardContent>
            </Card>
          </div>
        </div>
      </div>
    </div>
  );
}
